import React, { lazy, memo, Suspense } from 'react';

export const DynamicIcon = memo(({ icon }: { icon: string | undefined }) => {
  if (process.env.NODE_ENV === 'test') {
    return <div data-hook="icon-fallback" style={{ height: 18, width: 18 }} />;
  }

  if (!icon) {
    return null;
  }

  const ImportedIconComponent = lazy(() =>
    import(
      `@wix/wix-ui-icons-common/dist/esm/general/dist/components/${icon}.js`
    ).catch(() => {
      const defaultIconName = 'Edit';
      return import(
        `@wix/wix-ui-icons-common/dist/esm/general/dist/components/${defaultIconName}.js`
      ).catch(() => null);
    }),
  );

  return (
    <Suspense fallback={<FallbackIcon />}>
      <ImportedIconComponent />
    </Suspense>
  );
});

export const FallbackIcon = memo(() => (
  <div data-hook="icon-fallback" style={{ height: 18, width: 18 }} />
));
